.contracts-block{
	width: 300px;
	margin: 0 auto;
	margin-top: 20px;
}
.input{
	width: 280px;
	padding: 10px;
	font-size: 12px;
	font-weight: 300;
	margin-bottom: 15px;
}
.contact {	
	display: flex;
	justify-content: flex-start;
	align-items: center;
	background-color: #ffffe6;
	margin-top: 5px;
	padding: 10px;
}
.contact__gender{
	display: block;
	width: 30px;
	height: 30px;
	object-fit: cover;
}
.contact__data{
	display: flex;
	flex-direction: column;
	justify-content: center;	
	margin-left: 30px;
	font-family: 'Nunito', sans-serif;
	font-style: italic;
	text-align: left;
	color: #4d4d00;
}
.contact__fullName{
	display: inline-block;
	font-size: 18px;
	font-weight: 600;
	margin: 0;
}
.contact__phone{
	display: inline-block;
	font-size: 13px;
	font-weight: 300;
	margin: 0;
}
